.urgencySelection{
    width: 100%;
    height: 91.5px;
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 30px;
}
.urgency_selectOption{
    width: 100%;
    height: 30px;
    cursor: pointer;
    border-bottom: 0.5px solid black;
}
#urgency_selectLow{
    background-color: green;
}
#urgency_selectMedium{
    background-color: orange;
}
#urgency_selectHigh{
    background-color: red;
}